.navigation {
    padding: 17px 40px;;
    font-size: 30px;
    width: 930px;
    margin: 0 auto;
}

.navigation::after {
    content: '';
    display: table;
    clear: both;
}

.nav-content {
    text-align: center;
}

.nav-content-left {
    float: left;
    height: 100%;
}

.nav-content-right {
    float: right;
    margin: 0;
    padding: 0;
    list-style: none;

}

.nav-content-right li {
    display: inline-block;
    padding: 0 30px;
}

.nav-content-right a {
    color: #444;
    text-decoration: none;
}

.nav-content-left-logoicon {
    padding-right: 15px;
}

.nav-content-logo {
    padding-left: 15px;
    border-left:#939393 solid 1px;
    color: #444;
    text-decoration: none;
    font-family: 'Indie Flower', cursive;
}

.nav-content-center {
    display: inline-block;
}

.nav-content-center-search {
    width: 100%;
    padding: 6px 10px;
    border-radius: 10px;
    background-color: #fafafa;
    border: 1px solid #eee;
    text-align: center;
}

.nav-content-center-search:focus{
    text-align: left;
    outline: none;
}


