.card {
    background-color: white;
    border-radius: 3px;
    border: 1px solid #e6e6e6;
    margin-bottom: 60px;
}

.card-header {
    padding: 16px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.card-header-cover {
    float: left;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.card-header-title {
    margin-left: 12px;
}

.card-header-title-name a {
    font-weight: 600;
    font-size: 14px;
    color: black;
    text-decoration: none;
}

.card-header-title-name a:hover {
    text-decoration: underline;
}

.card-header-title-location {
    font-size: 12px;
}

.card-images {
    width: 100%;
    height: 500px;
    background-color: purple;
}

.card-footer {
    padding: 0 16px;
    text-align: left;
}

.card-icons {
    margin-top: 4px;
}

.card-icons::after {
    content: '';
    display: table;
    clear: both;
}

.card-icons a {
    color: #444;
}

.card-icons a i {
    padding: 8px;
}

.card-icons:nth-child(1) {
    margin-left: -8px;
}

.card-icons-left {
    float: left;
}

.card-icons-right {
    float: right;
}

.card-footer-hits {
    text-align: left;
}

.card-footer-content {
    text-align: left;
    word-break: break-all;
}

.card-footer-content-user {
    font-weight: bold;
    margin: 0 0.5rem 0 -1px;
}

.card-footer-content-text {

}

.card-footer-content a {
    color: #444;
    text-decoration: none;
}

.card-footer-content-more {
    font-size: 14px;
    cursor: pointer;
    color: #dddddd;
}

.card-footer-content-morerp {
    margin-bottom: 8px;
}

.card-footer-content-morerp span {
    font-size: 14px;
    cursor: pointer;
    color: #ddd;
}

.card-footer-rp {
    margin: 0;
    padding: 0;
    list-style: none;
}

.card-footer-rp li{
    padding: 5px 0 6px;
    margin-top: -5px;
}

.card-footer-rp a {
    margin-right: 5px;
    color: #444;
    text-decoration: none;
    font-weight: bold;
}

.card-footer-time {
    margin-bottom: 5px;
    font-size: 14px;
    color: #ddd;
}

.card-footer-comment {
    padding: 16px 26px 16px 0;

    border-top: #eee solid 1px;
}

.card-footer-comment-input {

    width: 100%;
    font-size: 14px;
    border: 0;
    outline: none;
}


