hr {
    border-top: 1px solid #efefef;
}

.sidebar {
    width: 293px;
    position: absolute;
    top: 60px;
    right: 0;
    height: 100vh;
}

.sidebar-story-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.sidebar-story-header span {
    color: #999;
    font-size: 14px;
}

.sidebar-story-header a {
    color: black;
    font-size: 12px;
    text-decoration: none;
}

.sidebar-footer {
    margin-bottom: 16px;
}

.sidebar-footer a {
    color: #d6d6d6;
    font-size: 11px;
    text-decoration: none;
}

.sidebar-footer a:hover {
    text-decoration: underline;
}

.sidebar-final {
    font-size: 11px;
    color: #d6d6d6;
}

.sidebar-story-list {
    height: 272px;
}


